import React from 'react';
import { useEffect, useState } from 'react';
import Card from '../components/Card';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';

import { useContext } from 'react';
import { SearchContext } from '../context/SearchContext';


function Search () {

    const { search } = useContext(SearchContext)

    const [movies, setMovies] = useState([])

    useEffect(() => {
        fetch('http://localhost:3001/movies')
            .then(response => response.json())
            .then(data => {
                setMovies(data)
            })
            .catch(error => console.error(error))
    }, [])

    const searchOk = search.toString().toLowerCase()

    return(

      <div >
      
       { search !== '' ? 

        <Container sx={{marginY: 5}} >
        <Grid container spacing={3}  >

          {movies.filter(movie => {
            if (movie.title.toString().toLowerCase().includes(searchOk)){
              return movie
            }
          }).map(movie => 
            <Card title={movie.title} rating={movie.rating} img={movie.img} length={ movie.length == null ? 0 : movie.length} id={movie.id} key={movie.id}  />
            )}
            
        </Grid>
        </Container>

      :

      <Container sx={{marginY: 5}} >
        <Grid container spacing={3}  >

          {movies.map((movie, i) => 
            <Card title={movie.title} rating={movie.rating} img={movie.img} length={ movie.length == null ? 0 : movie.length} id={movie.id} key={i}  />
            )}
            
        </Grid>
      </Container>
      } 
    </div>

      
      
      )} 

    export default Search;