import React from "react";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Container from '@mui/material/Container';
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import Rating from "@mui/material/Rating";



function DetailCard () {

    const [movies, setMovies] = useState([])

    useEffect(() => {
        fetch('http://localhost:3001/movies')
            .then(response => response.json())
            .then(data => {
                setMovies(data)
            })
            .catch(error => console.error(error))
    }, [])

    let { id } = useParams()
    /* let id = Number(props.match.params.id); */
    let movie = movies.find(movie => movie.id === Number(id));

    return(
        <Container sx={{ width: 900 }} >

            <Card elevation={5} sx={{marginTop: '60px'}} >

                <Typography component='h1' variant="h4" margin={3} sx={{marginLeft: '42px'}} >
                    {movie && movie.title}
                </Typography>

                <Box sx={{display: 'flex', margin: '45px' }} >

                    <img src={movie && movie.img} alt="" height={325} />

                    <Box >

                        <Box sx={{marginLeft: '35px'}} >
                            <Typography component='h4' variant="h6" marginTop={1} >
                                Descripcin:
                            </Typography>
                            <Typography component='p' variant='paragraph' marginTop={1} >
                                Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquam commodi ullam facere quasi aliquid pariatur nisi suscipit, amet ad minus numquam officiis a labore ipsam eos sapiente ratione neque iure?
                            </Typography>
                        </Box>

                        

                        <Box sx={{display: 'flex', marginTop: '20px'}} >
                            <Typography component='h1' variant='h6'  sx={{marginLeft: '35px'}}>
                                Genero: 
                            </Typography>
                            <Typography component='p' variant='paragraph' sx={{marginLeft: '8px', paddingTop: '7.5px'}} >
                                
                                {movie && movie.genre != null ? movie.genre.name : 'Sin Genero' }
                            </Typography>
                        </Box>

                        
                        <Box sx={{display: 'flex'}} >
                            <Typography component='h1' variant='h6' marginTop={1} sx={{marginLeft: '35px'}}>
                                Actores: 
                            </Typography>
                            <Typography component='p' variant='paragraph' sx={{marginLeft: '8px', paddingTop: '16px'}} >
                            {movie && movie.actors.map( actor => {
                                return <p> {actor.first_name} {actor.last_name} </p>
                                })}
                            </Typography>
                        </Box>

                        <Box sx={{display: 'flex', alignItems: 'center', margin: '15px', marginLeft: '35px'}} >

                            
                            {movie && <Rating name="read-only" value={Number(movie.rating)} precision={0.1} size='small' readOnly />}
                            

                            <Typography variant="body2" component={'p'} marginLeft={0.5} sx={{fontSize: '10px'}} >
                                ( {movie && movie.length} Min)
                            </Typography>

                        </Box>

                    </Box>

                </Box>

            </Card>

        </Container>
    )
}

export default DetailCard;