import React from 'react';
import { useEffect, useState } from 'react';
import Card from '../components/Card';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import { Typography } from '@mui/material';



function Genres () {

    const [genres, setGenres] = useState([])

    useEffect(() => {
        fetch('http://localhost:3001/genres')
            .then(response => response.json())
            .then(data => {
                setGenres(data)
            })
            .catch(error => console.error(error))
    }, [])

    return(
    <div >
      
            {/* {genres.map((genre, i) => 
            <div>

            <h2>{genre.name}</h2>
            <ul key={i}>
                
                 {genre.movies.map(movie => {
                    return <li> {movie.title} </li>} 
                    )}
                
            </ul>
            </div>
            )} */}

    <Container sx={{marginY: 5}} >

        {genres.map(genre => 
            <>
            {genre.movies.length === 0 ? '' :
            <Typography component='h1' sx={{fontSize: '50px', marginTop:'80px', marginBottom:'80px', textDecoration: 'underline'}}>
                        {genre.name}:
            </Typography>}

            <Grid container spacing={3} >
            
            {genre.movies.map((movie, i) => 
                <Card title={movie.title} rating={movie.rating} img={`http://localhost:3001/images/${movie.img}`} length={ movie.length == null ? 0 : movie.length} id={movie.id} key={i} />
                )}
                
            </Grid>
            </>
            )}



      </Container>
      
      
    </div>
    )} 

    export default Genres; 